.basic_info_container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}
.basicinfo_row {
    display: flex;
    flex-direction: column;
}
.basicinfo_content {
    display: flex;
    align-items: center;
}
.basicinfo_label {
    width: 25%;
    font-weight: 500;
    /* margin-top: 6px; */
}

.basicinfo_input {
    width: 70%;
    padding: 5px;
    /* margin-top: 6px; */
}
textarea {
    height: 150px;
}
.basicinfo_input,
select,
textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
}

button[type="submit"] {
    background-color: #4caf50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
}

button[type="submit"]:hover {
    background-color: #45a049;
}
.error_message {
    color: #d8000c;
}
