.chuandaura_top_btn button:not(:first-child) {
    margin-left: 10px;
}
.chuandaura_content {
    margin-top: 10px;
    padding-left: 10px;
}

.chuandaura_content ol {
    counter-reset: section;
    list-style-type: none;
}

.chuandaura_content li:before {
    counter-increment: section;
    content: counters(section, ". ") " ";
    font-weight: 500;
    background-color: #c2daf8;
    text-align: center;
    color: rgb(51, 51, 51);
    padding: 2px 5px 2px 8px;
    margin-right: 5px;
}

.chuandaura_content ol > li {
    display: table;
    /* counter-increment: item; */
    margin-bottom: 0.6em;
    width: 90%;
    margin-top: 10px;
}

.chuandaura_content ol > li:before {
    /* display: table-cell; */
    padding-right: 0.3em;
}
